.status {
  max-width: 1062px;
  margin-left: auto;
  padding: 0 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 14px;
  line-height: 20px;
  color: #0A243F;
  margin-bottom: 37px;
}

.status__left {
  display: flex;
  align-items: center;
}

.status__type {
  display: flex;
  align-items: center;
  margin-left: 20px;
  color: #0F47C0;
}
.status__type img {
  margin-right: 2px;
}

.status__right {
  display: flex;
  align-items: center;
}
.status__right .form-switch {
  margin-left: 20px;
}

.form-check-label, .form-check-input {
  cursor: pointer;
}

.form-switch {
  padding-left: 3em;
}
.form-switch .form-check-input {
  width: 2.5em;
  margin-left: -3em;
  margin-top: 0.15em;
}
.form-switch .form-check-input:checked {
  background-color: #0F47C0;
  border-color: #0F47C0;
}
.form-switch .form-check-input {
  height: 1.4em;
}

@media (max-width: 991px) {
  .status {
    padding: 0;
  }
}
@media (max-width: 767px) {
  .status {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 15px;
  }

  .status__left {
    margin-bottom: 20px;
  }
}
@media (max-width: 374px) {
  .status__right {
    flex-direction: column;
  }

  .status__right .form-switch {
    margin-top: 15px;
    margin-left: 0;
  }
}
.exp-content {
  display: flex;
  align-items: flex-start;
}

.exp-content__sidebar {
  flex: 0 0 247px;
  margin-right: 63px;
}

.exp-content__edit {
  flex: 1;
  padding: 12px;
  background-color: #fff;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

.sidebar__img {
  width: 230px;
  height: 230px;
  border-radius: 100%;
  overflow: hidden;
  border: 6px solid #FFFFFF;
  background-size: cover;
  background-position: center;
  cursor: pointer;
  transition: 0.3s all;
  margin-bottom: 24px;
}

.sidebar__img:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
}

.sidebar {
  margin-top: 42px;
}

.sidebar__name {
  margin-bottom: 92px;
  font-weight: bold;
  font-size: 18px;
  line-height: 28px;
}

.sidebar__nav ul {
  margin: 0;
  padding: 0;
}

.sidebar__nav li {
  list-style: none;
  margin: 0;
  margin-bottom: 8px;
}

.sidebar__nav a {
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  color: #AFACB9;
}

.sidebar__nav a:hover {
  text-decoration: underline;
}

.sidebar__nav .sidebar__nav__menu1 {
  padding-bottom: 8px;
  border-bottom: 1px solid #AFACB9;
  max-width: 205px;
  margin-bottom: 26px;
}

.sidebar__nav__menu1 a {
  color: #092C9E;
}

.sidebar__nav li.last a {
  color: #092C9E;
  display: flex;
}
.sidebar__nav li.last a svg {
  margin-left: 8px;
}

.exp-content__top {
  font-weight: bold;
  font-size: 24px;
  line-height: 36px;
  color: #F4F7FF;
  padding: 12px 27px;
  border-radius: 10px 10px 0 0;
  background: #0F47C0;
  margin-bottom: 12px;
}

.exp__about {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding-bottom: 16px;
  padding-top: 32px;
  background-size: cover;
  margin-bottom: 7px;
}

.exp__about__img {
  width: 158px;
  height: 158px;
  border: 6px solid #FFFFFF;
  border-radius: 100%;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  margin-bottom: 16px;
}

.btn-primary {
  background-color: #0F47C0;
  border-color: #0F47C0;
}

.btn {
  font-size: 16px;
  line-height: 24px;
  border-radius: 20px;
  padding: 0.45rem 1.4rem;
  font-weight: bold;
}

.about__sett {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-left: 22px;
  margin-bottom: 22px;
}

.about__sett__left {
  display: flex;
  align-items: center;
}

.custom-select {
  font-family: inherit;
}

.about__sett__color {
  font-size: 12px;
  line-height: 12px;
  font-weight: bold;
  padding: 5px 1px;
  color: #FFFFFF;
  border-radius: 2px;
  margin-right: 4px;
}

.about__sett__right {
  width: 250px;
}

.select-selected {
  background: #FFFFFF;
  border: 1px solid #A1A4B1;
  box-sizing: border-box;
  border-radius: 20px;
  font-size: 14px;
  line-height: 24px;
  color: #8B90A0;
  padding: 11px 16px;
  padding-right: 40px;
}

.select-selected:after {
  border-color: #A1A4B1 transparent transparent transparent;
  top: 21px;
  right: 23px;
}

.select-selected.select-arrow-active:after {
  border-color: transparent transparent #A1A4B1 transparent;
  top: 16px;
}

.select-selected.select-arrow-active {
  border: 1px solid #2B5CFB;
  box-sizing: border-box;
  /* Shadow/Level1 */
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.15);
  border-radius: 20px 20px 0px 0px;
  color: rgba(0, 0, 0, 0.8);
}

.select-items {
  border: 1px solid #2B5CFB;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.15);
  border-radius: 0px 0px 4px 4px;
  background: #FFFFFF;
}

.select-items div {
  font-size: 14px;
  line-height: 24px;
  color: #8B90A0;
}

.select-items div:hover, .select-items .same-as-selected {
  color: #092C9E;
}

.select-items div:hover {
  background-color: #fff;
}

.select-items .same-as-selected:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.about__sett__left .select-selected {
  border: 0;
  padding: 0;
  font-weight: bold;
  font-size: 18px;
  line-height: 28px;
  color: rgba(0, 0, 0, 0.8);
}
.about__sett__left .select-selected:after {
  position: initial;
  display: inline-block;
  border: 0;
  border-color: transparent;
  border-bottom: 1.5px solid #000;
  border-right: 1.5px solid #000;
  width: 10px;
  height: 10px;
  transform: rotate(45deg) translateY(-5px);
  margin-left: 3px;
}

h3 {
  font-weight: bold;
  font-size: 32px;
  line-height: 40px;
  color: #0A243F;
}

.exp-content__title {
  padding-left: 25px;
  border-bottom: 1px solid #A1A4B1;
}

.exp-content__wrap {
  padding-left: 25px;
  max-width: 736px;
  padding-top: 16px;
  margin-bottom: 53px;
}

.exp-content__wrap--big {
  max-width: 954px;
}

.exp-content__row {
  display: flex;
  align-items: flex-start;
  margin-bottom: 10px;
}

.exp-content__row:last-child {
  margin-bottom: 0;
}

.exp-content__row__left {
  flex: 0 0 240px;
  padding-top: 5px;
  padding-right: 15px;
}

.label {
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  color: #0A243F;
}
.label span {
  color: var(--color-red);
}

.input {
  background: #FFFFFF;
  border: 1px solid #A1A4B1;
  border-radius: 20px;
  height: 40px;
  padding: 8px 14px;
  width: 100%;
  margin-bottom: 5px;
  font-size: 15px;
  line-height: 24px;
  color: #18171C;
}

.input.input--custom-radius {
  border-radius: 10px;
  margin-bottom: 15px;
}

.input::placeholder {
  color: #AFACB9;
}

.input.error {
  border: 2px solid #F2A60D;
  color: #F2A60D;
}

.error-text {
  color: #F2A60D;
  font-size: 14px;
  line-height: 20px;
  margin-top: -5px;
  margin-bottom: 10px;
}

.exp-content__row__right {
  flex: 1;
}
.exp-content__row__right .btn {
  margin-top: 20px;
}

.exp-link {
  font-size: 12px;
  line-height: 18px;
  text-decoration-line: underline;
  color: #A1A4B1;
  display: inline-block;
  margin-bottom: 5px;
}

.form-check__row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.form-check--check {
  margin-bottom: 15px;
  margin-top: 10px;
  margin-right: 59px;
}
.form-check--check .form-check-input {
  border: 2px solid #C3CBCD;
  width: 1.4em;
  height: 1.4em;
  margin-top: 0.1em;
}
.form-check--check .form-check-label {
  font-size: 15px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.8);
  margin-left: 5px;
}
.form-check--check .form-check-input:checked[type=radio] {
  background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'><circle r='2' fill='%230F47C0'/></svg>");
}
.form-check--check .form-check-input:checked {
  background-color: #fff;
  background-size: 120%;
  border: 2px solid var(--color-blue);
}

.form-check--check:last-child {
  margin-right: 0;
}

.textarea {
  margin-bottom: 15px;
}
.textarea textarea {
  min-height: 100px;
  width: 100%;
  background: #FFFFFF;
  border: 1px solid #A1A4B1;
  border-radius: 10px;
  padding: 8px 14px;
  display: block;
  max-height: 300px;
}
.textarea .textarea__maxlength {
  font-size: 12px;
  line-height: 18px;
  color: #A1A4B1;
  margin-top: 5px;
}
.textarea textarea::placeholder {
  color: #AFACB9;
}

.textarea--big textarea {
  min-height: 125px;
}

::-webkit-resizer {
  width: 10px;
  height: 10px;
  background-image: url("../img/about/textarea-resize.svg");
  background-size: 8px;
  background-repeat: no-repeat;
  background-position: left top;
}

.from-to {
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  color: #0A243F;
}
.from-to .from-to__line {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.from-to .from-to__col1 {
  flex: 0 0 25px;
}
.from-to .from-to__col2 {
  flex: 0 0 170px;
  margin-right: 8px;
}

@media (max-width: 1400px) {
  .exp-content__sidebar {
    margin-right: 25px;
  }
}
@media (max-width: 991px) {
  .exp-content__sidebar {
    flex: 0 0 200px;
  }

  .sidebar__img {
    width: 200px;
    height: 200px;
  }

  .exp-content__top {
    font-size: 18px;
    line-height: 1.3;
  }

  .about__sett {
    flex-direction: column;
    padding-left: 0px;
  }

  .sidebar__nav a {
    font-size: 14px;
    line-height: 20px;
  }

  .sidebar__name {
    font-size: 15px;
    line-height: 22px;
    margin-bottom: 50px;
  }

  .sidebar {
    margin-top: 20px;
  }

  .about__sett__left {
    margin-bottom: 15px;
  }

  .exp-content__row {
    flex-direction: column;
  }

  .exp-content__row__left {
    flex: auto;
    margin-bottom: 10px;
  }

  .exp-content__row__right {
    width: 100%;
  }

  .exp-content__wrap {
    padding-left: 0;
  }

  .exp-content__title {
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .exp-content {
    flex-direction: column;
  }

  .exp-content__sidebar {
    flex: auto;
    width: 100%;
    margin-right: 0;
    margin-bottom: 25px;
  }

  .sidebar {
    display: flex;
    align-items: flex-start;
  }

  .sidebar__top {
    margin-right: 25px;
    max-width: 150px;
  }

  .sidebar__img {
    width: 150px;
    height: 150px;
    margin-bottom: 15px;
  }

  .sidebar__name {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 15px;
  }

  .sidebar__nav .sidebar__nav__menu1 {
    margin-bottom: 15px;
    max-width: initial;
  }

  h3 {
    font-size: 24px;
    line-height: 1.4;
  }
}
@media (max-width: 480px) {
  .sidebar {
    flex-direction: column;
  }

  .sidebar__top {
    width: 100%;
    max-width: initial;
    margin-bottom: 25px;
  }

  .sidebar__img {
    margin-left: auto;
    margin-right: auto;
  }

  .sidebar__name {
    text-align: center;
  }

  .sidebar__nav li {
    margin-bottom: 5px;
  }

  .exp-content__row__left {
    margin-bottom: 5px;
  }

  .about__sett__left .select-selected {
    font-size: 16px;
    line-height: 28px;
  }
}